/* Navbar Css */
.navbar-brand{
  padding-left: 35px;
  position: relative;
}
.navbar-dark .navbar-nav .nav-link{
  color:unset; 
  background-color: unset;   
}
.nav-item ,.nav-link{
color: white;
}
.navbar-dark .navbar-nav .nav-item:hover{
color:white;
background-color:#FF4900 ;
border-radius: 5px;
 
}
.navbar-dark .navbar-nav .nav-link:hover{
color:white;
}

.active {
background-color: #FF4900;
color: white;
border-radius: 5px;
margin-right: 2px;
}

/* use before and after slector for navbar name */
.animbrand a{
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;   
} 
.animbrand a:after {
  content: '/>';
  position: absolute;
  opacity: 0;  
  top: 5px;
  right: -30px;
  transition: 0.5s;
  color:white;
}
.animbrand a:before {
  content: '<';
  position: absolute;
  opacity: 0;  
  top: 5px;
  left: -10px;
  transition: 0.5s;
  color:white;
}
  .animbrand a:hover{
    padding-right: 24px;
    padding-left:24px;
  }

.animbrand a:hover:after {
  opacity: 1;
  right: 0px;
}
.animbrand a:hover:before {
  opacity: 1;
  left: 7px;
}
/* navbar list margin */


/* navbar autohide while scrolling */

.autohide{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  z-index: 1030;
}
.scrolled-down{
  transform:translateY(-100%); transition: all 0.3s ease-in-out;
}
.scrolled-up{
  transform:translateY(0); transition: all 0.3s ease-in-out;
}

